.footer{display:grid;grid-template-columns:auto 1fr;align-items:center;padding:1rem 0}@media(min-width: 1024px){.footer{padding-bottom:2rem;grid-template-columns:1fr auto 1fr}}.footer__color-scheme-button{background-color:rgba(0,0,0,0);border:none;margin-left:1rem;grid-column:1}.footer__color-scheme-button:hover svg path:nth-child(1){fill:var(--support);stroke:var(--foreground);stroke-width:3}@media(min-width: 1024px){.footer__color-scheme-button{margin-right:1rem}}.footer__nav-menu-footer{grid-column:2;width:100%;padding:0 .5rem}@media(min-width: 375px){.footer__nav-menu-footer{padding:0 1rem}}@media(min-width: 1024px){.footer__nav-menu-footer{padding:0}}.footer__nav-link{font-size:.75rem;text-transform:uppercase;border:var(--support) solid 2px;border-right:none;display:inline-block;padding:1rem .5rem;text-align:center;width:33%}.footer__nav-link:last-child{border-right:var(--support) solid 2px}@media(min-width: 375px){.footer__nav-link{padding:1rem}}@media(min-width: 1024px){.footer__nav-link{font-size:.875rem;min-width:300px;max-width:320px}}